#countdown {
  width: 250px;
  padding: 10px;
  background-color: #FFFFFF99;
  backdrop-filter: blur(5px);
  border-radius: 20px;
  border: 0.5px solid #C70101;
  box-shadow: 0 0 15px #45141844;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  margin: auto;
  flex-direction: row;
  margin-top: -5vh;
}
#countdown .box {
  padding: 10px;
  border-right: solid 1px rgba(255, 255, 255, 0.2);
}
#countdown .col-4:last-child .box {
  border-right-color: transparent;
}
#countdown .box p {
  font-size: 32px;
  font-weight: bold;
  margin: 0;
  color: #C70101;
}
#countdown .box .text {
  font-size: 12px;
  font-family: sans-serif;
  color: #C70101;
}

@media (min-width: 768px) {
  .container {
    width: 1100px;
  }
  h1 {
    font-size: 58px;
  }
  #countdown {
    width: 350px;
    padding: 20px;
  }
}
